<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item prop="accountId">
      <el-select  v-model="ruleForm2.accountId" value-key="id" filterable style="width:100%;">
      <el-option v-for="item in whsOrgList" :key="item.id" :label="item.name" :value="item">
      </el-option>
      </el-select>
    </el-form-item>
    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
  </el-form>
</template>

<script>
  import {userLogin} from '../api/api';
  import httpUtil from "@/common/utils/HttpUtil";
  import permission from "@/common/utils/Permission";
  //import NProgress from 'nprogress'
  export default {
    data() {
      return {
        logining: false,
        ruleForm2: {
          account: '',
          checkPass: '',
       },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true,
        whsOrgList:[]
      };
    },
    created(){
      this.getWhsOrgList();
    },
    methods: {
      handleReset2() {
        this.$refs.ruleForm2.resetFields();
      },
      handleSubmit2(ev) {
        var _this = this;
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            //_this.$router.replace('/table');
            if(!_this.ruleForm2.accountId){
              _this.$message.error('请先选择一个仓库。');
              return;
            }

            _this.logining = true;
            //NProgress.start();
            var loginParams = { 
              username: _this.ruleForm2.account, 
              password: _this.ruleForm2.checkPass,
              accountId:_this.ruleForm2.accountId.id,
              account:_this.ruleForm2.accountId
              // accountId:_this.ruleForm2.accountId 
             };
            userLogin(loginParams,data=>{
               
               //_this.$router.push({ path: '/' });
               //_this.$commonUtil.tab.closeAll(_this);
               var href = window.location.href;
               if(href.includes("login")){
                 href = href.replace('login','');
               }
               window.location.href = href;
               window.location.reload();
            }).then(
              res=>{
                _this.logining = false;
              }
            ).catch(
              err=>{
                _this.logining = false;
              }
            );
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      getWhsOrgList: function(){
        httpUtil.post("whsOrg/getAllWhsOrg", null, data => {
            this.whsOrgList = data;
        });
      } 
    }
  }

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 100px auto 0 auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>


